import {createContext, useState} from "react";

// her state bir initial state'e sahip olmaldr!
export const AuthContext = createContext({}); // depo create

// props.children => react tarafndan oto. bu component tagleri arasna yazlan yapy tar.
export const AuthProvider = (props: any) => {
	const [isAuthenticated, setIsAuthenticated] = useState(false);

	return (
		<AuthContext.Provider value={{isAuthenticated, setIsAuthenticated}}>
			{props.children}
		</AuthContext.Provider>
	);
}; // depoyu uygulamaya salayacak component

